mes also were not held during World War\n                I and World War II.")]),_c('p',[_vm._v("Highland Games have been picked up across continents with the first Highland Games in the USA\n                happening in New York in 1836. The Caledonian Club of San Francisco held its first Games in 1866 and\n                boasts the oldest continuously running Games in the USA with the St Andrews Society of Detroit as a\n                close second.")])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseTrig","aria-expanded":"false","aria-controls":"collapseTrig"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingTrig"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Trig Setup")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseTrig","aria-labelledby":"headingTrig","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"http://scottishheavyathletics.com/img/therules_img_0.jpg"}}),_c('p',{staticClass:"mt-2"},[_vm._v("Standard Trig - The traditional trig setup is 4ft 6in in width. There are two back lines. The first line at 7ft 6in is the throwing area an athlete can use for open stone throws. The second line at 9ft is the throwing area for weight for distance events. ")]),_c('img',{attrs:{"src":"http://scottishheavyathletics.com/img/therules_img_1.jpg"}}),_c('p',{staticClass:"mt-2"},[_vm._v("Winged Trig - The Winged Trig setup is becoming more popular. The back lines are the same distances as the Standard Trig. The biggest difference is that the throwing area is pushed out to 6ft 9in and narrows to the traditional 4ft 6in in the front. This trig setup originally was used during foul weather to give the athletes a larger throwing area. Another benefit to the winged trig is that it is easier to judge. If the athlete touches the line or goes over it, the throw is no good or \"a scratch\".")])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseOne","aria-expanded":"false","aria-controls":"collapseOne"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingOne"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Braemar")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseOne","aria-labelledby":"headingOne","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://s.hdnux.com/photos/45/13/42/9746608/3/360x0.jpg","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("This event is similar to the modern-day shot put as seen in the Olympic games.\n              Instead of a steel shot, a large stone of variable weight is often used. There are also some differences\n              from the Olympic shot put in allowable techniques. The “Braemar Stone” uses a 20–26 lb (9-11.7 kg) stone\n              for men. Women us a 13–18 lb (5.89-8.16 kg). Braemar throws do not allow any run up to the toeboard or\n              “trig” to deliver the stone, i.e., it is a standing put with no forward movement.")])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseTwo","aria-expanded":"false","aria-controls":"collapseTwo"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingTwo"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Open Stone")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseTwo","aria-labelledby":"headingTwo","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"http://scottishheavyathletics.com/img/highland/stone_opt.jpg","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("This event is similar to the modern-day shot put as seen in the Olympic games. Instead of a steel\n                shot, a large stone of variable weight is often used. There are also some differences from the Olympic\n                shot put in allowable techniques. In the “Open Stone” using a 16–22 lb (7.25 - 9.98 kg) stone for men.\n                Women used an 8–12 lb (3.63 - 5.44 kg) stone. Utilizing the forward section of the trig (7ft 6in x 4ft\n                6in) area. The thrower is allowed to use any throwing style so long as the stone is put with one hand\n                with the stone resting cradled in the neck until the moment of release. Most athletes in the open\n                stone event use either the “glide” or the “spin” techniques.\n              ")])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseThree","aria-expanded":"false","aria-controls":"collapseThree"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingThree"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Weight for Distance")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseThree","aria-labelledby":"headingThree","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://www.eveningtelegraph.co.uk/wp-content/uploads/sites/11/2019/08/stuart-anderson-weight-for-distance.jpg","width":"100%","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("The Weight for Distance event consists of a weight made of metal and consist of a spherical or box\n                weight with a chain and handle attached. The handle may be a ring, triangle, or a \"D\" shape. The\n                implement shall not measure more than 18 inches in overall length from the bottom of the weight to the\n                top of the handle. The official weight of each implement is the total weight, including the handle.\n                The\n                competitor may use either hand to throw the weight, but only one hand may be used. Any style may be\n                used\n                to throw the weight as long as it is deemed safe by the judge. The competitor may start and stop the\n                throw provided that no part of the implement has made contact with the ground or the trig board during\n                the aborted attempt. If the implement has made contact with the ground or the trig board after the\n                attempt was initiated and prior to receiving permission from the judge, then the attempt will be a\n                foul.\n\n                Standard weights for these two events divided by throwing classes:")]),_c('ul',[_c('li',[_vm._v("Mens Pros and semi pros throw a 28 lb (kg) light weight and 56 lb (kg) heavy weight.")]),_c('li',[_vm._v("Mens Amatures (A, B, C classes) throw a 28 lb (kg) light weight and 56 lb (kg) heavy weight.")]),_c('li',[_vm._v("Masters, Novice, and Lightweight competitors throw a 42 lb (kg) heavy weight.")]),_c('li',[_vm._v("Women Amatures (open A, B, C classes) throw a 14 lb light weight and a 28 lb heavy weight.")]),_c('li',[_vm._v("Women Masters and Women Lightweight throw a 21 lb heavy weight.")])])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseFour","aria-expanded":"false","aria-controls":"collapseFour"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingFour"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Scottish Hammer")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseFour","aria-labelledby":"headingFour","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://www.charlotteobserver.com/news/local/community/lake-norman-mooresville/lr8dh7/picture19209501/alternates/FREE_960/LKN_MPRICE_HIGHLANDGAMES_10.JPG","width":"100%","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("This event is similar to the hammer throw as seen in modern-day track and field competitions, though\n                with some differences. In the Scottish event, a round metal ball weighing 16 lb (kg) for light and 22\n                lb\n                (kg) for heavy for the men classes. Women throw a 12 lb (kg) light hammer and 16 lb (kg) heavy hammer.\n                The hammer weight is attached to the end of a shaft about 4 feet in length and made out of wood,\n                bamboo,\n                rattan, or plastic. The athlete will have thier back to the field. With the feet in a fixed position,\n                the hammer is whirled about one’s head and thrown for distance over the shoulder. Hammer throwers\n                sometimes employ specially designed footwear with flat blades to dig into the turf to maintain their\n                balance and resist the centrifugal forces of the implement as it is whirled about the head. This\n                substantially increases the distance attainable in the throw.\n              ")]),_c('img',{attrs:{"src":"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQkqB4os9O-HhfLzTctyuz6tf0SvNz_67IMKO1ltgSJnYoU5DS9","alt":"Collage-Image"}})])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseFive","aria-expanded":"false","aria-controls":"collapseFive"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingFive"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Weight Over Bar (WOB)")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseFive","aria-labelledby":"headingFive","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://www.floridamemory.com/fpc/folklife/FS97422.jpg","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("The athletes attempts to toss weight with an attached handle over a horizontal bar using only one\n                hand.\n                Each athlete is allowed three attempts at each height. Successful clearance of the height allows the\n                athlete to advance into the next round at a greater height. The competition is determined by the\n                highest\n                successful toss with fewest misses being used to break tie scores. The weight for this event is also\n                class specific:\n                "),_c('ul',[_c('li',[_vm._v("Mens Professionals and semi professionals throw a 56 lb (kg) weight.")]),_c('li',[_vm._v("Mens Amatures (A, B, C classes) throw a 56 lb (kg) weight.")]),_c('li',[_vm._v("Masters, Novice, and Lightweight competitors throw a 42 lb (kg) weight.")]),_c('li',[_vm._v("Women Amatures (open A, B, C classes) throw a 28 lb weight.")]),_c('li',[_vm._v("Women Masters and Women Lightweight throw a 21 lb weight.")])])])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseSix","aria-expanded":"false","aria-controls":"collapseSix"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingSix"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Caber")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseSix","aria-labelledby":"headingSix","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://www.thesun.co.uk/wp-content/uploads/2018/10/NINTCHDBPICT000424764714.jpg","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("The Caber Toss is the signature event of the Highland Games. A Caber (gaelic for \"Tree\") is a tree\n                that\n                has been cut and trimmed down so one end is slightly wider than the other. It can vary length from 12\n                to\n                22 feet and between 50 and 180 pounds. The objective of this event is for the athlete to flip a \"12\n                o’clock” (which is considered a perfect toss) meaning that the athlete will \"pick\" the caber (the act\n                of\n                leaning down and popping the caber up into their hands), run with the caber and then attempt to flip\n                it\n                end over end so that it lands perfectly straight in front of them or pointing to 12 o'clock on an\n                imaginary clock face on the ground. Points are awarded from 9 o'clock to 3 o'clock, and even if the\n                athlete does not flip the caber they are awarded points for how far it goes over before it falls back\n                in\n                degrees.\n              ")]),_c('img',{attrs:{"src":"http://www.nasgaweb.com/images/toss_off_linew.gif","alt":"Collage-Image","width":"100%"}})])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseSeven","aria-expanded":"false","aria-controls":"collapseSeven"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingSeven"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Sheaf Toss")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseSeven","aria-labelledby":"headingSeven","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"http://www.gmhg.org/Tossing%20the%20Sheaf/JJS-7873a.jpg","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("The sheaf toss is the only \"non-traditional\" Highland sport event originally contested at country\n                fairs. A pitchfork is used to hurl a burlap bag stuffed with straw over a horizontal bar above the\n                competitor's head. Typical weight for the bag is 16 lb (kg) for men. Women typically use a 10 lb (kg)\n                bag.\n              ")])])])]),_c('Section',{staticClass:"card mt-3 collapsed",attrs:{"data-toggle":"collapse","data-target":"#collapseEight","aria-expanded":"false","aria-controls":"collapseEight"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingEight"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Bagpiping Competition")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseEight","aria-labelledby":"headingEight","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://ichef.bbci.co.uk/images/ic/480xn/p01m1rhx.jpg","width":"100%","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("There are six different grades in bagpiping: V - I (five through one are amateur grades) plus\n                \"Professional\" which is sometimes called \"Open.\" (\"Open\" competions are open to any grade level of\n                competitor, though lackluster pipers are not expected to subject the judges to a poor standard of\n                play.) Some associations also break grades further into \"Juvenile\" and \"Adult\" or \"Junior\" and\n                \"Senior.\" Below is a loose description of the grades, though you can expect different opinions since\n                there is no written standard. One piping judge with 40 years of piping experience described any\n                attempt to define grades as \"impossible.\" (A certain amount of piping appreciation is subjective and\n                therefore a set standard would be hard for judges to implement.) These are descriptions of a typical\n                player in the grade, not necessarily the\n                ones winning that may belong in a higher grade.")]),_c('p',[_vm._v("\n                Grade V (five): This is the lowest grade (sometimes not recognized or offered) and is the rank\n                beginner level. At this grade, in some associations (such as WUSPBA), performers are merely\n                competing using a practice chanter. In other associations (such as EUSPBA), performers can be on a\n                full set of pipes. Grade V pipers may or may not be required to belong to their association to\n                compete as these events may or may not be sanctioned. Competitors typically try to get through the\n                tune without any glaring errors. Tunes are played very slowly. Many pipers skip any \"chanter only\"\n                grade.")]),_c('p',[_vm._v("\n                Grade IV (four): Pipers are playing on a full set of pipes. At any given time, about 60%-70% of\n                competing pipers that you run into are going to be in this grade (or in Grade V). Slow tunes are\n                played, and quick tunes are played at a reduced tempo. Competitors may not be able to tune their own\n                drones perfectly. Only partial piobaireachd tunes are played in competition. Occasional minor\n                embellishment mistakes. Competitors who don't make technical errors, but don't have particularly\n                good expression might win. Drones not always steady throughout the tune. An attempt at musicality\n                and expression is made and sometimes achieved. Most associations don't offer MSRs (March,\n                Strathspey, Reel) at this level.")]),_c('p',[_vm._v("\n                Grade III (three): Competitors can tune their own drones pretty accurately. Complete piobaireachd\n                tunes are played in competition. Good execution of fingering. MSRs, sometimes only SRs, are played\n                slowly at this grade. Only about 25%-30% of pipers ever advance to this grade. Drones are steady.\n              ")]),_c('p',[_vm._v("\n                Grade II (two): Command of many tunes, judges can select from several submitted. Drones are steady\n                and tuned very well, if not perfect. MSRs just about at tempo. Very good embellishments, musicality\n                and expression.")]),_c('p',[_vm._v("\n                Grade I (one): Excellent playing, everything just about perfect. Performance is just shy of\n                professional quality.")]),_c('p',[_vm._v("\n                Professional: All aspects of performance are consistently outstanding: drones stay steady and tuned\n                for long periods of playing, lovely expression, crisp and clear embellishments, very fast tempo on\n                demand.")])])])]),_c('Section',{staticClass:"card mt-3 collapsed mb-3",attrs:{"data-toggle":"collapse","data-target":"#collapseNine","aria-expanded":"false","aria-controls":"collapseNine"}},[_c('div',{staticClass:"card-header",attrs:{"id":"headingNine"}},[_c('h2',{staticClass:"mb-0"},[_c('button',{staticClass:"btn btn-link",attrs:{"type":"button"}},[_vm._v("Irish Dancing")])])]),_c('div',{staticClass:"collapse",attrs:{"id":"collapseNine","aria-labelledby":"headingNine","data-parent":"#accordionExample"}},[_c('div',{staticClass:"card-body"},[_c('img',{attrs:{"src":"https://mediad.publicbroadcasting.net/p/wmuk/files/styles/x_large/public/201307/AP070317029794.jpg","width":"100%","alt":"Collage-Image"}}),_c('p',{staticClass:"mt-2"},[_vm._v("Irish dance or Irish dancing is a group of traditional dance forms originating from Ireland,\n                encompassing dancing both\n                solo and in groups, and dancing for social, competitive, and performance purposes. Irish dance in its\n                current form\n                developed from various influences such as French quadrilles and English country dancing throughout the\n                18th and 19th\n                centuries. Dance was taught by \"travelling dance masters\" across Ireland throughout this period, and\n                separate dance\n                forms developed according to regional practice and differing purposes. Irish dance became a\n                significant part of Irish\n                culture, particularly for Irish nationalist movements. From the early 20th century, a number of\n                organisations promoted\n                and codified the various forms of dance, creating competitive structures and standardised styles.")]),_c('p',[_vm._v("Solo Irish dance includes the most well-known form of Irish dance, Irish stepdance, which was\n                popularised from 1994\n                onwards by dance shows such as Riverdance, and which is practised competitively across the Irish\n                diaspora. Stepdance is\n                characterised by the rigid upper body and intricate footwork of its performers. Other forms of solo\n                Irish dance include\n                sean-nós dance, a relaxed and social dance style involving improvised steps, and festival Irish dance,\n                a style which\n                separated from step dance in the mid-20th century.")]),_c('p',[_vm._v("Irish dancing in groups is made up of a number of styles and traditions, which developed from French\n                and English dances\n                and formations. Ceili dance, practised both competitively and socially, is performed by groups of two\n                to sixteen people,\n                and often uses traditional or codified dances and formations. Its footwork is simple, and emphasis is\n                placed on the\n                figures and formations of the dances. Set dance is primarily a social tradition, for groups of four\n                dancers, and\n                includes elements of the intricate footwork found in step dance.\n              ")])])])])],1),_c('div',{staticClass:"col-md-3"})])])])}
